import React from "react";
import { Box, Typography, useTheme } from "@mui/material";
import KeyboardDoubleArrowDownIcon from "@mui/icons-material/KeyboardDoubleArrowDown";
import { Link } from "react-scroll";
import nevenImage from "../assets/images/neven.jpg";

const Landing = () => {
  const theme = useTheme();

  return (
    <Box
      sx={{
        marginTop: "64px", //    Header
        position: "relative", //    
        "&::before": {
          content: '""', //   ::before  ::after
          position: "absolute", //      Box 
          top: -40, //      
          left: 0, //    
          width: "100%", //  
          height: "100%", //  
          bgcolor: "background.section", //    (  )
          zIndex: -1, //   
          transform: "skewY(-6deg)", //  
          transformOrigin: "top left", //   
        },
        minHeight: "calc(100vh - 64px)", //       Header
        display: "flex", //  
        alignItems: "center", //  
        pb: 15, // padding-bottom
        [theme.breakpoints.down("md")]: {
          //   
          flexDirection: "column",
          pb: 25,
        },
      }}
    >
      <Box
        sx={{
          flex: 1, //    
          pl: { xs: 2, md: 10 }, // padding-left 
          pr: { xs: 2, md: 5 }, // padding-right 
          [theme.breakpoints.down("md")]: {
            //  
            width: "100%",
            textAlign: "center",
            pl: 2,
            pr: 2,
          },
        }}
      >
        <Typography
          variant="h1"
          sx={{
            maxWidth: "800px",
            mx: "auto",
          }}
        >
          <Box
            component="span"
            sx={{
              mb: 1, // margin-bottom
              display: "block", //   
              borderRight: "1px solid", //   
              borderColor: "transparent", //     
              overflow: "hidden", //   
              whiteSpace: "nowrap", //   
              animation:
                "control-width 3s steps(24) 1s both, blink 1s infinite",
              [theme.breakpoints.down("md")]: { mx: "auto" }, //   
            }}
          >
            Neven Ali
          </Box>
          Software Engineer
        </Typography>
        <Typography
          variant="body1"
          sx={{
            maxWidth: "800px", //      
            mx: "auto", //      maxWidth
            textAlign: { xs: "justify", md: "justify" }, //  
            textAlignLast: { xs: "center", md: "left" }, //   
            p: { xs: 2, md: 0 }, // padding 
            mt: 2, // margin-top
          }}
        >
          Telecommunications engineer turned frontend developer. I transform
          Figma designs into pixel-perfect React interfaces with clean code and
          optimal performance. My HIAST engineering background brings analytical
          precision to UI development, creating interfaces that work as
          beautifully as they look. Proficient in HTML, CSS, JavaScript, and
          modern frontend workflows, I bridge the gap between design and
          technical execution.
        </Typography>
      </Box>
      <Box
        component="img"
        src={nevenImage}
        alt="Neven Ali"
        sx={{
          p: { xs: 2, md: 7 }, // padding 
          width: { xs: 300, md: 400 }, //  
          borderRadius: "50%", //  
          position: "relative", //  
          animation: "up-and-down 4s linear infinite", //  
          filter: "drop-shadow(2px 4px 6px black)", // 
          [theme.breakpoints.down("md")]: { maxWidth: 350 }, //   
        }}
      />
      <Link to="services" smooth={true} duration={500}>
        <Box
          sx={{
            position: "absolute", //  
            bottom: 50, //  
            left: "50%", //  
            transform: "translate(-50%)", //  
            display: { xs: "none", md: "block" }, //   
          }}
        >
          <KeyboardDoubleArrowDownIcon
            sx={{
              fontSize: 40,
              color: "primary.main",
              animation: "bouncing 1.5s infinite", //  
            }}
          />
        </Box>
      </Link>
    </Box>
  );
};

export default Landing;
