import React, { useEffect } from "react";
import { Box, Typography, LinearProgress } from "@mui/material";
const Languages = () => {
  useEffect(() => {
    const skills = document.querySelectorAll(".skill-progress");
    if (skills.length > 0) {
      skills.forEach((skill) => {
        skill.style.width = skill.dataset.width;
      });
    }
  }, []);
  return (
    <Box
      id="Languages"
      sx={{
        marginTop: "64px", //    Header
        py: 10,
        bgcolor: "background.section",
        position: "relative",
        
        "&::before": {
          content: '""',
          position: "absolute",
          width: "100%",
          height: 30,
          left: 0,
          top: 0,
          backgroundImage:
            "linear-gradient(135deg, white 25%, transparent 25%), linear-gradient(225deg, white 25%, transparent 25%)",
          backgroundSize: "30px 30px",
          zIndex: 2,
        },
      }}
    >
      <Typography
        variant="h2"
        sx={{
          textTransform: "uppercase", //     
          mx: "auto", //   
          mb: 8, //   (8 * 8px = 64px)
          width: "fit-content", //    
          fontSize: 27, //  
          p: "10px 20px", // padding: 10px  20px 
          border: "2px solid", //   2px
          borderColor: "secondary.main", //    
          position: "relative", //    
          zIndex: 100, //      
          transition: "all 0.3s ease-in-out", //    
          "&:before, &:after": {
            //      
            content: '""', //    
            width: 12, //   
            height: 12, //  
            position: "absolute", //     
            bgcolor: "primary.main", //   
            borderRadius: "50%", //  
            top: "50%", //    
            transform: "translateY(-50%)", //  
            transition: "all 0.3s ease-in-out", //   
            zIndex: -1, //   
          },
          "&:before": { left: -30 }, //     
          "&:after": { right: -30 }, //     
          "&:hover": {
            color: "white", //     
            borderColor: "white", //     
            "&:before": {
              left: 0, //      
              width: "100%", //   51%  
              height: "100%", //    
              border: "0",
              borderRadius: 0, //     
            },
            "&:after": {
              right: 0, //      
              width: "100%", //   49% 
              height: "100%", //   
              border: "0",
              borderRadius: 0, //   
            },
          },
        }}
      >
        Languages
      </Typography>

      <Box
        sx={{
          maxWidth: 900,
          mx: "auto",
          px: 4,
          transform: "translateY(-25px)",
        }}
      >
        <Box sx={{ mb: 4 }}>
          <Box sx={{ display: "flex", justifyContent: "space-between", mb: 1 }}>
            <Typography
              variant="h3"
              sx={{ position: "relative", fontSize: 22 }}
            >
              Arabic
            </Typography>
            <Typography
              sx={{
                color: "primary.main",
                border: "1px solid white",
                fontSize: 17,
                p: "3px 5px",
                borderRadius: 1,
              }}
            >
              Native
            </Typography>
          </Box>
          <LinearProgress
            variant="determinate"
            value={100}
            sx={{
              height: 40,
              bgcolor: "#eee",
              "& .MuiLinearProgress-bar": {
                bgcolor: "primary.main",
                transition: "width 0.5s linear",
              },
            }}
          />
        </Box>

        <Box sx={{ mb: 4 }}>
          <Box sx={{ display: "flex", justifyContent: "space-between", mb: 1 }}>
            <Typography
              variant="h3"
              sx={{ position: "relative", fontSize: 22 }}
            >
              English
            </Typography>
            <Typography
              sx={{
                color: "primary.main",
                border: "1px solid white",
                fontSize: 17,
                p: "3px 5px",
                borderRadius: 1,
              }}
            >
              Fluent
            </Typography>
          </Box>
          <LinearProgress
            variant="determinate"
            value={70}
            sx={{
              height: 40,
              bgcolor: "#eee",
              "& .MuiLinearProgress-bar": {
                bgcolor: "primary.main",
                transition: "width 0.5s linear",
              },
            }}
          />
        </Box>
      </Box>
    </Box>
  );
};
export default Languages;
